    width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --primary-text-color: #000;
  --secondary-text-color: #b92f2c;
  --bgr-btn-color: rgba(36, 92, 177, .77);
  --akcent-text-color: #fff;
  --footer-text-color: #545454;
  --primary-bg-color: #f0f0f0;
  --footer-bg-color: #f7f7f7;
  --background-header-radient: linear-gradient( 273.67deg, rgba(0, 0, 0, .49) -2.09%, rgba(0, 0, 0, 0) 99.83% );
  --overlay-bg-color: rgba(0, 0, 0, .2);
  --font-family: "Roboto", sans-serif;
}

.spinner {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner:after {
  content: "";
  width: 100px;
  height: 100px;
  border: 10px solid rgba(0, 0, 0, .3);
  border-top-color: #b92f2c;
  border-radius: 50%;
  animation: loading 1s cubic-bezier(0, 0, .2, 1) infinite;
}

@keyframes loading {
  from {
    transform: rotate(0turn);
  }

  to {
    transform: rotate(1turn);
  }
}

.wrapper__pagination {
  justify-content: center;
  align-items: center;
  margin-bottom: 60px;
  display: flex;
}

.pagination {
  font-family: var(--font-family);
  color: var(--primary-text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
